.blog-container {
    padding: 90px 10px;
}

.blog-content {
    max-width: 1110px;
    margin-left: auto;
    margin-right: auto;
}

.icon-text {
    display: flex;
    gap: 6px;
}

.icon-text span {
    margin-top: auto;
    margin-bottom: auto;
}

.icon-blog-post {
    width: 100%;
    max-width: 20px;
    max-height: 16px;
    height: 100%;
    display: inline-block;
}

.blog-posts {
    display: flex;
    gap: 24px;
}

.blog-card {
    padding: 21px;
    height: 235px;
    max-width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.blog-card>h2 {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    margin-bottom: 12px;
}

.blog-meta {
    display: flex;
    gap: 28px;

}

.blog-meta span {
    height: auto;
}


@media (max-width: 918px) {

    .blog-meta {
        flex-direction: column;
        gap: 8px
    }

    .blog-posts {
        gap: 15px;
    }

}

@media (max-width: 610px) {

    .blog-posts {
        flex-direction: column;
        gap: 40px;
    }

    .blog-posts span {
        margin-left: auto;
        margin-right: auto;
    }

    .blog-meta {
        flex-direction: row;
    }

    .blog-card>h2 {
        text-align: center;
    }
}